.App {
  text-align: center;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.app {
  height: 100vh;
}

.header {
  background-color: #252831;
  color: #e4eaed;
  margin: 0;
  padding: 0;
  text-align: center;
}

.header-title {
  margin: 0 40px 0 60px;
  padding: 20px;
  font-size: 1.6rem;
  display: inline-block;
  max-width: 350px;
}

@media screen and (max-width: 350px) {
  .header-title {
    font-size: 11px;
    margin: 0 5px 0 50px;
  }
}

.toggle-list-button {
  font-size: 1.8rem;
  margin: 15px;
  cursor: pointer;
  position: absolute;
  left: 1px;
  top: 15px;
}

.toggle-list-button:hover {
  color: #e9e8e5d7;
}

main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row nowrap;
      flex-flow: row nowrap;
  height: 87%;
}

@media screen and (max-width: 495px) {
  #main {
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
  }
}

.filter-container {
  background-color: #252831;
  border: 1px solid #39474d; width: 50%;
  max-width: 330px;
  overflow-y: auto;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: block;
  height: calc(100vh - 135px);
  max-height: 892px;
  min-width: 320px;
  position: relative;
  -webkit-transition: left 2.3s ease-out;
  -o-transition: left 2.3s ease-out;
  transition: left 2.3s ease-out;
}

.toggle-element {
  position: fixed;
  left: 0;
}

.filter-input-container {
  margin: 15px;
  display: block;
}

.filter-input {
  padding: 10px;
  margin: auto;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 1rem;
  display: block;
}

.list-items {
  list-style-type: none;
  text-align: left;
  margin: 0;
  padding: 0;
  line-height: 1.6;
  font-size: 0.96rem;
}

.list-items li {
  padding: 3px 5px 3px 20px;
  display: block;
  opacity: 1;
  border-top: 1px solid rgba(255, 255, 255, .3); 
  border-bottom: 1px solid rgba(0, 0, 0, .1);
  color: #252831;
  background-color: #e2d9bff3;
  visibility: hidden;
  -webkit-animation: 1s fade-in;
          animation: 1s fade-in;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

.list-items li:hover,
.list-items li:focus {
  background-color: #e1d2a9f3;
}

#map {
  width: 100%;
  height: calc(100vh - 135px);
}

.footer {
  background-color: #252831;
  text-align: center;
  color: #e4eaed;
  display: block;
  position: fixed;
  bottom: 0;
  right: 0;
  margin: auto;
}

.footer-text {
  margin: 5px auto 5px;
  padding: 1px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  justify-items: center;
}

@media screen and (max-width: 640px) {
  .footer-text {
    height: 40px;
    padding: 20px 10px 15px;
    font-size: 0.8rem;
  }
  .header-title {
    font-size: 1.4rem;
    margin-left: 30px;
  }
}

.attribution {
  margin: 5px;
}

.attribution a {
  margin-right: 5px;
}

.attribution a {
  list-style-type: none;
  color: #3397db;
}

.attribution a:hover {
  color: #268bf7c8;
}

.venue-name {
  font-size: 1.4rem;
  padding: 5px;
}

.venue-category {
  font-size: 1.1rem;
  color: #3a3a34bd;
  padding: 5px;
}

.venue-address {
  font-size: 0.9rem;
  padding: 5px;
}

/* Spinner generated at https://projects.lukehaas.me/css-loaders/ */
.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation: spin 1.8s infinite ease-in-out;
  animation: spin 1.8s infinite ease-in-out;
}

.loader {
  color: #ffffffeb;
  font-size: 9px;
  margin: 35vh auto;
  position: relative;
  text-indent: -9999em;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}

.loader:before {
  left: -3.5em;
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.loader:after {
  left: 3.5em;
}

@-webkit-keyframes spin {
  0%,
  80%,
  100% {
    -webkit-box-shadow: 0 2.5em 0 -1.3em;
            box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    -webkit-box-shadow: 0 2.5em 0 0;
            box-shadow: 0 2.5em 0 0;
  }
}

@keyframes spin {
  0%,
  80%,
  100% {
    -webkit-box-shadow: 0 2.5em 0 -1.3em;
            box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    -webkit-box-shadow: 0 2.5em 0 0;
            box-shadow: 0 2.5em 0 0;
  }
}


